body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.favButton{
  background-color: rgba(136, 136, 136, 0.116);
  padding: 10px;
  border: solid;
  width: 50%;
  cursor: pointer;
  border-radius: 25px;
}

h1{
  text-align: center;
}

 nav{
  display: flex;
  width: 80vw;
  justify-content: flex-start;
  padding: 1rem;
} 

 a{
  color: #ffffff;
  text-decoration: none;
  margin: 5px;
  cursor: pointer;
}
a:hover{
  color: rgb(52, 38, 38);
}  

 footer {
  display: flex;
  justify-content: center;
  text-align: center;
  /* padding: 2rem; */
  position: relative;
  bottom: 0;
  width: 100%;
  height: 40px;
  /* margin-top: 2rem; */
  
} 

footer > img{
  width: 200px;
  margin: 0;
}
footer > p{
  margin: 0;
  /* padding-top: 0.7rem; */
}

form{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 300px;
  height: 100px;
  margin: 0 auto;
}

.card{
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 0.5rem 0.5rem 0 0.5rem;
  border: 0.5px solid rgb(136, 136, 136);
}

.card:hover{
  border: 0.5px solid rgb(206, 217, 142);
}

.card-grid{
  display: flex;
  justify-content: space-evenly;
  gap: 1rem;
  width: 90vw;
  height: auto;
  flex-wrap: wrap;
  margin: 0 auto;
}

table {
 border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    display: table;
    border: 1px solid #ccc;

}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.dark{
  background-color: #12121296;
  color: white;
}
.dark > a {
  color: white;
} 

.light{
  background-color: #ffeeeedb;
  color: rgb(0, 0, 0);
}
.light > a {
  color: rgb(255, 255, 255);
}

.buttonTheme {
  border-radius: 25px;
  height: 40px;
}

